<template>
    <div class="card m-2 btn themed-btn p-0" @click="getTo()" :class="short ? 'short' : 'space'">
        <img :src="require('../assets/img/' + person.code + '.jpg')" class="card-img-top">
        <div class="card-body">
            <h6 class="card-title" v-if="person.title">{{ person.title }}</h6>
            <p class="card-text">{{ person.shortName }}</p>
        </div>
    </div>
</template>
<script>
export default {
    name: 'Person',
    props: {
        person: Object,
        short: true
    },
    methods: {
        getTo() {
            const vm = this;
            let url;
            if (!vm.short) {
                if (vm.person.url) {
                    url = vm.person.url;
                } else {
                    url = `parampara/${vm.person.code}`;
                }
                vm.$router.push(url);
            } else {
                vm.$emit('goTo');
            }
        }
    }
}
</script>
<style lang="scss" scoped>
.space {
    min-width: 170px;
    
    img {
        height: 280px;
    }
}

.short {
    min-width: 90px;
}
</style>