<template>
    <div class="events w-sm-100 w-md-35 w-lg-35">
        <div class="input-group input-group-lg mb-3">
            <button class="btn btn-outline-secondary" @click="toggleClose"
                 v-if="!calendar.isDateSelected">
                <i class="fa fa-close"></i>
            </button>
            <button class="btn btn-outline-secondary" @click="calendar.onDateSelected(calendar.today)"
                 title="Go to Today" v-else>
                <i class="fa fa-undo"></i>
            </button>
            <input type="text" v-model="searchText" id="search" class="form-control flex-fill" placeholder="Search event"
                   @click="calendar.setIsDateSelected(false)">
            <button class="btn btn-outline-secondary"
                 @click="calendar.setIsDateSelected(false); calendar.setFilteredEvents(calendar.calDates);"
                 title="View all events">
                <i class="fa fa-list"></i>
            </button>
        </div>
        <div class="list-container">
            <div v-if="calendar.isDateSelected">
                <DayEvents :day="calendar.selectedDate" :calendar="calendar"/>
                <div v-if="calendar.selectedDate._upcoming">
                    <div class="fw-bold upcoming d-flex">Upcoming Events</div>
                    <div v-for="(day, dayIndex) in calendar.today._upcoming" :key="dayIndex">
                        <DayEvents :day="day" :calendar="calendar"/>
                    </div>
                </div>
            </div>
            <div v-else>
                <div v-for="(day, dayIndex) in calendar.filteredEvents" :key="dayIndex">
                    <DayEvents :day="day" :calendar="calendar"/>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DayEvents from './DayEvents.vue';

export default {
    name: 'CalEventList',
    components: {
        DayEvents
    },
    props: {
        calendar: Object
    },
    data() {
        return {
            searchText: ''
        }
    },
    methods: {
        toggleClose() {
            const vm = this;

            vm.searchText = '';
            vm.calendar.onDateSelected(vm.calendar.selectedDate);
            vm.calendar.setFilteredEvents({});
        }
    },
    watch: {
        searchText() {
            const vm = this;

            if (vm.searchText !== '') {
                vm.calendar.filterEvents(vm.searchText);
            } else {
                vm.calendar.setFilteredEvents({});
            }
        }
    }
}
</script>
<style lang="scss" scoped>
$all: ('', 0, 4000px);
$sm: ('-sm', 0, 767.98px);
$md: ('-md', 768px, 991.98px);
$lg: ('-lg', 992px, 4000px);

$breakpoints: ($all, $sm, $md, $lg);
@mixin sm {
    @media screen and (min-width: nth($sm, 2)) and (max-width: nth($sm, 3)) {
        @content;
    }
}

@mixin md {
    @media screen and (min-width: nth($md, 2)) and (max-width: nth($md, 3)) {
        @content;
    }
}

@mixin lg {
    @media screen and (min-width: nth($lg, 2)) and (max-width: nth($lg, 3)) {
        @content;
    }
}

.events {
    min-width: 200px;
    overflow: visible;
    
    @include sm {
        margin-top: 10px;
    }

    .list {
        margin: 20px 0 10px 5px;
        border-radius: 3px;
        overflow: hidden;
    }

    .upcoming {
        font-size: 20px;
        margin-top: 50px;
        border-bottom: 1px solid grey;
    }
}

.list-container {
    @include lg {
        max-height: calc(100vh - 150px);
    }

    @include md {
        max-height: calc(100vh - 205px);
    }
}

// tablet + desktop
@media screen and (min-width: 768px) {
    .list-container {
        overflow-y: auto;
        padding-right: 5px;
    }
}
</style>