<template>
    <div>
        <article v-if="person.name">
            <h2>{{ person.name }}</h2>
            <div class="d-flex justify-content-center mb-3">
                <div class="img"
                     :style="{ 'background-image': 'url(' + require('../assets/img/' + person.code + '.jpg') + ')' }"></div>
            </div>
            <div v-html="person.desc" class="p"></div>
        </article>
    </div>
</template>
<script>
import Person from '../components/Person.vue';

export default {
    name: 'Personality',
    components: {
        Person
    },
    data() {
        return {
            person: {}
        }
    },
    created() {
        const vm = this;
        vm.loadPersonality(vm.$route.params);
    },
    methods: {
        loadPersonality(params) {
            const vm = this;
            import('../assets/js/parampara')
                .then((data) => {
                    const personalities = data.default;
                    for (let i = 0; i < personalities.length; i++) {
                        if (personalities[i].code === params.code) vm.person = personalities[i];
                    }
                })
        }
    },
    watch: {
        $route(to) {
            this.loadPersonality(to.params);
        }
    }
}
</script>
<style lang="scss" scoped>
article {
    max-width: 650px;
    margin: 0 auto;

    h2 {
        text-align: center;
    }

    .img {
        height: 250px;
        width: 150px;
        border-radius: 10px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
    }

    .p {
        margin-bottom: 25px;
        font-size: 18px;
        line-height: 25px;
        text-indent: 50px;
    }
}
</style>