<template>
    <div class="d-flex flex-column list" id="list">
        <div v-if="!$route.params.code" class="d-flex flex-column flex-fill justify-content-center">
            <div class="d-flex">
                <Person :person="person" :clickable="true" v-for="(person, personIndex) in list" :key="personIndex"/>
            </div>
        </div>
        <div v-else>
            <router-view></router-view>
        </div>
    </div>
</template>
<script>
import _ from 'lodash';
import Person from '../components/Person.vue';

export default {
    name: 'Personalities',
    components: {Person},
    data() {
        return {
            list: []
        }
    },
    created() {
        var vm = this;
        vm.initList();
    },
    mounted() {
        const el = document.getElementById('list');
        el.onwheel = (event) => {
            el.scrollLeft += event.deltaY;
        }
    },
    methods: {
        initList(category) {
            var vm = this;
            vm.list = [];
            import('../assets/js/parampara')
                .then(dataImport => {
                    vm.list = _.cloneDeep(dataImport.default);
                })
        }
    }
}
</script>
<style lang="scss" scoped>
.list {
    @media screen and (min-width: 0px) and (max-width: 991.98px) {
        height: calc(100vh - 110px);
        max-width: 100vw;
        overflow-x: auto;
    }
}
</style>