<template>
    <div class="d-flex flex-column month w-sm-100 w-md-65 w-lg-65 px-md-3 px-lg-3 px-xl-3">
        <div class="d-flex" v-if="calendar.location.name">
            <div class="d-flex flex-fill justify-content-center fw-bold mb-2 day"
                 v-for="(day, dayIndex) in calendar.shortDays"
                 :class="{ 'fc-red' : dayIndex === 0 }" :key="dayIndex">{{ day }}
            </div>
        </div>
        <div class="d-flex flex-fill" v-for="(week, weekIndex) in calendar.currentMonth.weeks" :key="weekIndex">
            <div v-for="(day, dayIndex) in week" :key="dayIndex"
                 :class="{ selectedDate: day && calendar.selectedDate._dateText === day._dateText,
                        today: day && calendar.today._dateText === day._dateText, 'fc-red' : dayIndex === 0 }"
                 class="flex-fill btn d-flex justify-content-center align-items-center date clickable"
                 :style="{ '--eventBgImg': day && day.url ? day.url : '' }"
                 @click="() => { calendar.onDateSelected(day) }">
                {{ day._date }}
                <div class="dot red d-flex justify-content-center align-items-center"
                     v-if="day && day.ekadashi"></div>
                <div class="dot blue d-flex justify-content-center align-items-center"
                     v-if="day && day.events && ((day.events.length > 0) || (day.special))">
                    {{ day.special ? day.events.length + 1 : day.events.length }}
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'CalMonth',
    props: {
        calendar: Object
    }
}
</script>
<style lang="scss" scoped>
$all: ("", 0, 4000px);
$sm: ("-sm", 0, 767.98px);
$md: ("-md", 768px, 991.98px);
$lg: ("-lg", 992px, 4000px);

$breakpoints: ($all, $sm, $md, $lg);
@mixin sm {
    @media screen and (min-width: nth($sm, 2)) and (max-width: nth($sm, 3)) {
        @content;
    }
}

@mixin md {
    @media screen and (min-width: nth($md, 2)) and (max-width: nth($md, 3)) {
        @content;
    }
}

@mixin lg {
    @media screen and (min-width: nth($lg, 2)) and (max-width: nth($lg, 3)) {
        @content;
    }
}

.dark .date {
    background-color: rgba(50, 50, 50, 0.6);
}

.month {
    z-index: 0;
}

.date,
.day {
    @include lg {
        width: 6vw;
    }

    @include md {
        width: 10vw;
    }

    @include sm {
        width: 14vw;
    }
}

.date {
    border-radius: 10px;
    position: relative;

    @include lg {
        height: 6vw;
    }

    @include md {
        height: 10vw;
    }

    @include sm {
        height: 14vw;
    }

    &::after {
        content: " ";
        position: absolute;
        z-index: -1;
        border-radius: 10px;
        background-image: var(--eventBgImg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        width: 100%;
        height: 100%;
    }
}

.dot {
    position: absolute;
    width: 15px;
    height: 15px;
    border-radius: 7.5px;
    margin-top: 30px;

    &.red {
        background-color: #FA5858;
        margin-left: -30px;
    }

    &.blue {
        background-color: goldenrod;
        margin-left: 30px;
        font-size: 12px;
        font-weight: bold;
        color: white;
    }
}

.fc-red {
    color: red;
}

.today,
.dark .today {
    background-color: rgba(255, 165, 0, 0.7);

    &:hover {
        background-color: rgba(255, 165, 0, 0.8);
    }
}

.selectedDate,
.dark .selectedDate {
    &:not(.today) {
        background-color: rgba(255, 165, 0, 0.4);
    }

    .selectedDate:not(.today):hover {
        background-color: rgba(255, 165, 0, 0.5);
    }
}
</style>