<template>
    <div class="d-flex flex-column flex-lg-row flex-xl-row" :data-bs-theme="dark ? 'dark' : 'light'" :class="dark ? 'dark' : ''">
        <nav class="navbar navbar-light bg-light d-flex d-lg-none d-xl-none">
            <div class="container-fluid d-flex align-items-center">
                <a class="navbar-brand flex-fill d-flex justify-content-center" href="/#/"><h1 class="fs-4 m-0">Sri Chaitanya Saraswat Math</h1></a>
            </div>
        </nav>
        <div class="bars" :class="menuDisplay ? 'd-flex' : 'd-none d-lg-flex d-xl-flex'">
            <div class="d-flex flex-column p-3 bg-light sidebar">
                <a href="/#/" class="d-none d-lg-flex d-xl-flex mb-3 mb-md-0 me-md-auto link-dark text-decoration-none w-100">
                    <h1 class="fs-4 d-flex justify-content-center align-items-center w-100">Sri Chaitanya Saraswat Math</h1>
                </a>
                <div class="d-flex acharya justify-content-around">
                    <div v-for="person in persons" :key="person.code">
                        <Person
                            :person="person"
                            :short="true"
                            @goTo="goTo('parampara/' + person.code)"
                        />
                    </div>
                </div>
                <hr>
                <ul class="nav nav-pills d-none d-lg-flex d-xl-flex flex-column mb-auto">
                    <li class="nav-item" v-for="page in pages" :key="page.code">
                        <a :href="`/#/${page.code}`" class="nav-link" @click="() => { menuDisplay = false; }" :class="$route.path.includes('/' + page.code) ? 'active' : 'link-dark'">
                            {{ page.name }}
                        </a>
                    </li>
                </ul>
                <hr class="d-none d-lg-flex d-xl-flex">
                <div>
                    <div class="form-check form-switch">
                        <input class="form-check-input" type="checkbox" id="flexSwitchCheckDefault" v-model="dark" @click="toggleDarkMode">
                        <label class="form-check-label" for="flexSwitchCheckDefault">Dark mode</label>
                    </div>
                </div>
            </div>
            <div class="rest" @click="menuDisplay = false"></div>
        </div>
        <nav class="navbar navbar-light fixed-bottom bg-light d-flex d-lg-none d-xl-none align-items-start justify-content-around mob-nav">
            <button type="button" class="btn" @click="menuDisplay = true"  :class="menuDisplay ? 'active' : ''">
                <i class="fa fa-bars"></i>
                <div>Menu</div>
            </button>
            <a :href="`/#/${page.code}`" @click="() => { menuDisplay = false; }" class="btn"
               v-for="page in pages" :key="page.code"
               :class="$route.path.includes('/' + page.code) ? 'active' : ''">
                <i :class="`fa fa-${page.icon}`"></i>
                <div>{{ page.shortName }}</div>
            </a>
        </nav>
        <router-view class="view flex-fill"/>
    </div>
</template>
<script>
// @ is an alias to /src
import Person from "../components/Person.vue";

export default {
    name: "home",
    components: {Person},
    data() {
        return {
            showSidepanel: false,
            menuDisplay: false,
            widthLT960: false,
            dark: false,
            persons: [
                {code: 'GovindaMhj', title: 'Sevaite Acharya', shortName: 'Srila B.S. Govinda Maharaj'},
                {code: 'SridharMhj', title: 'Founder Acharya', shortName: 'Srila B.R. Sridhar Maharaj'}
            ],
            pages: [
                {
                    name: 'Vaishnava Calendar',
                    shortName: 'Calendar',
                    code: 'calendar',
                    icon: 'calendar'
                },
                {
                    name: 'Books',
                    shortName: 'Books',
                    code: 'books',
                    icon: 'book'
                },
                {
                    name: 'Guru Parampara',
                    shortName: 'Gurus',
                    code: 'parampara',
                    icon: 'users'
                }
            ]
        };
    },
    created() {
        var vm = this;
        
        if (localStorage.dark) {
            vm.dark = JSON.parse(localStorage.dark);
        }

        if (window.innerWidth < 860) {
            vm.widthLT960 = true;
        }
    },
    methods: {
        logOut() {
            localStorage.clear();
            location.reload();
        },
        goTo(name) {
            var vm = this;
            if (vm.$route.path != "/" + name) {
                vm.$router.push("/" + name);
            }
            vm.menuDisplay = "menu-display-none";
        },
        getImg(code) {
            var vm = this;
            let mobOpt = vm.widthLT960 ? "-mob" : "";
            return {
                "background-image": code
                    ? "url(" + require("../assets/img/" + code + mobOpt + ".jpg") + ")"
                    : "",
            };
        },
        toggleDarkMode() {
            localStorage.dark = JSON.stringify(!this.dark);
        }
    },
};
</script>
<style lang="scss">
.dark {
    &.home-container,
    .menu-button,
    .view{
        background-color: rgb(50, 50, 50) !important;
        color: rgb(225, 225, 225);
    }
    
    a,
    a:hover,
    button,
    .themed-btn {
        color: rgb(225, 225, 225) !important;
    }
    
    .navbar {
        background-color: rgb(60, 60, 60) !important;
    }
    
    .sidebar {
        background-color: rgb(50, 50, 50) !important;
    }

    .navbar,
    .sidebar {
        color: rgb(225, 225, 225) !important;
    }
    
    .card {
        border-radius: 10px !important;
        overflow: hidden !important;
    }
}

.active,
.form-check-input:checked {
    color: #000 !important;
    border-color: orange !important;
    background-color: orange !important;
}

.form-check-input:focus {
    outline: 0 !important;
    border: 0 !important;
}

.themed-btn {
    color: #000 !important;
    border-color: rgba(255, 165, 0, 0.7) !important;

    &:hover {
        background-color: rgba(255, 165, 0, 0.7) !important;
    }
}

.form-select,
.btn-group,
.form-control {
    border: 1px solid #6c757d !important;
}

.sidebar a:not(.active),
.btn-group .btn {
    &:hover {
        color: #fff !important;
        background-color: #6c757d !important;
    }
}

body {
    margin: 0;
}

.mob-nav {
    button,
    a {
        max-width: 25%;
        
        div {
            font-size: 12px;
        }
    }
}

// Desktop
@media screen and (min-width: 992px) {
    .view {
        height: 100vh;
        overflow-y: auto;
        z-index: 3;
        min-height: calc(100vh - 54px);
    }

    .sidebar {
        min-width: 25vw;
        width: 25vw;
        height: 100vh;
        background: white;
        overflow-x: hidden;
        z-index: 2;

        .content {
            max-height: 90vh;
            overflow: auto;
        }
    }
}

// Mobile + Tablet
@media screen and (max-width: 991.98px) {
    .view {
        z-index: 0;
        margin-bottom: 50px;
        min-height: calc(100vh - 123px);
        max-height: calc(100vh - 123px);
        overflow-y: auto;
    }

    .bars {
        position: fixed;
        width: 100vw;
        height: 100vh;
        max-height: 100vh;
        z-index: 1;
        top: 0;
        left: 0;
        overflow-y: auto;
        flex-direction: column;
        background-color: rgba(0, 0, 0, 0.3);
    }

    .sidebar {
        left: 0;
        width: 75vw;
        height: 100vh;
        overflow: auto;
        background: white;
        margin-bottom: 50px;
    }

    .rest {
        position: fixed;
        min-height: 100vh;
        width: 25vw;
        top: 0;
        right: 0;
    }

    .navbar {
        .active,
        .dark .btn.active {
            color: rgba(255, 165, 0, 1) !important;
            border-color: transparent !important;
            background-color: transparent !important;
        }
    }
}
</style>