<template>
    <div class="d-flex flex-column">
        <div class="d-flex flex-column h-100" v-if="isPdf">
            <embed :src="require('../assets/pdf/' + $route.params.code + '.pdf')" type="application/pdf" width="100%"
                   height="100%"/>
        </div>
        <div v-else-if="bookJSON && bookJSON.title">
            <div class="book">
                <div class="d-flex">
                    <div class="paragraph-list">
                        <section v-for="(chapter, chapterId) in bookJSON.chapters" class="paragraph-section m-2 p-4" :id="chapterId">
                            <div class="paragraph" v-for="paragraph in chapter.paragraphs">
                                <div v-html="paragraph.text" :class="paragraph.class"></div>
                            </div>
                        </section>
                    </div>
                    <div :class="menuDisplay ?
                        'paragraph-title-list d-flex position-fixed top-0 start-0 w-50 z-3' : 'd-none'"
                         @click="menuDisplay = false"
                    ></div>
                    <div class="paragraph-title-list"
                         :class="menuDisplay ? 'd-flex position-fixed top-0 end-0 w-50 z-3' :
                         'd-none d-md-flex d-lg-flex d-xl-flex'">
                        <ul class="list-group list-group-flush w-100">
                            <li class="list-group-item list-group-item-action"
                               v-for="(chapter, chapterId) in bookJSON.chapters"
                               :class="chapter.activeFlag ? 'active' : ''"
                               @click="scrollToSection(chapterId)" >
                                {{ chapter.title || (chapterId === 0 ? 'Cover' : '-- No title --') }}
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        <button class="btn btn-lg btn-primary rounded-circle position-fixed end-0 m-4 shadow d-md-none d-lg-none d-xl-none p-0 d-flex justify-content-center align-items-center active" @click="menuDisplay = true">
            <i class="fa fa-list"></i>
        </button>
    </div>
</template>
<script>
import _ from 'lodash';
import '@/assets/books/style/book-text.scss';
import '@/assets/books/style/font.scss';
import '@/assets/books/style/main.scss';

export default {
    name: 'ViewBook',
    data() {
        return {
            isPdf: false,
            bookJSON: {},
            currentSection: null,
            menuDisplay: false
        }
    },
    computed: {
        code() {
            return this.$route.params.code;
        }
    },
    created() {
        const vm = this;

        if (!vm.$route.query.isPdf) {
            vm.loadBook();
            return;
        }

        vm.isPdf = true;
    },
    methods: {
        async loadBook () {
            const vm = this;
            const bookImport = await import('@/assets/books/json/en-' + vm.code + '.json');

            vm.bookJSON = bookImport.default;
            if (window.innerWidth > 767) {
                vm.$nextTick(() => {
                    vm.setupIntersectionObserver();
                });
            }
        },
        scrollToSection(chapterId) {
            const element = document.getElementById(chapterId);
            const vm = this;

            if (element) {
                element.scrollIntoView({ behavior: "smooth", block: "start" });
                vm.menuDisplay = false;
            }
        },
        setActive(chapterId) {
            const vm = this;

            _.each(vm.bookJSON.chapters, (c, cIndex) => {
                vm.$set(c, 'activeFlag', ('' + cIndex) === ('' + chapterId));
            });
        },
        setupIntersectionObserver() {
            const vm = this;
            const sections = document.querySelectorAll('.paragraph-section');
            const container = document.querySelector('.paragraph-list');
            const options = {
                root: container, // Use the viewport as the root
                rootMargin: '0px',
                threshold: 0.01, // Trigger when 1% of the section is visible
            };

            const observer = new IntersectionObserver((entries) => {
                entries.forEach((entry) => {
                    if (entry.isIntersecting) {
                        vm.setActive(entry.target.id); // Update currentSection with the visible section's ID
                    }
                });
            }, options);

            // Observe each section
            sections.forEach((section) => {
                observer.observe(section);
            });
        }
    }
}
</script>
<style lang="scss" scoped>
.btn.rounded-circle {
    width: 50px;
    height: 50px;
    color: rgb(50, 50, 50) !important;
} 

.paragraph {
    padding: 10px;
}

.paragraph-title-list {
    min-width: 20vw;
}

.paragraph-title-list {
    &.position-fixed {
        height: calc(100vh - 71px);
    }
    
    &.start-0 {
        background-color: rgba(0, 0, 0, 0.5);
    }
    
    &.end-0 {
        background-color: #212529;
    }
}

.paragraph-title-list a {
    font-size: 1.5em;
    padding: 10px;
}

// Tablet
@media screen and (min-width: 768px) and (max-width: 991.98px) {
    .paragraph-list,
    .paragraph-title-list {
        overflow-y: auto;
        height: calc(100vh - 123px);
    }
}

// Desktop
@media screen and (min-width: 992px) {
    .paragraph-list,
    .paragraph-title-list {
        overflow-y: auto;
        height: 100vh;
    }
}

.paragraph-section {
    box-shadow: 0 0 10px grey;
}

.dark .book .paragraph-list .paragraph-section .paragraph * {
    color: white !important;
}

.list-group-item {
    cursor: pointer;
}

button.rounded-circle {
    bottom: 100px;
}
</style>