<template>
    <div class="p-2">
        <div class="d-flex flex-column-reverse flex-md-row flex-lg-row flex-xl-row">
            <div class="d-flex w-sm-100 w-md-65 w-lg-65 px-md-3 px-lg-3 px-xl-3 mb-3" v-if="calendar.location.name">
                <div class="btn-group btn-group-lg flex-fill" role="group">
                    <button type="button" class="btn" @click="calendar.currentMonth.setPrev()"><i
                        class="fa fa-caret-left"></i></button>
                    <button type="button" class="btn">
                        {{ calendar.currentMonth.name }} {{ calendar.currentMonth.year }}
                    </button>
                    <button type="button" class="btn" @click="calendar.currentMonth.setNext()"><i
                        class="fa fa-caret-right"></i></button>
                </div>
            </div>
            <div class="w-sm-100 w-md-35 w-lg-35 mb-3">
                <select class="form-select form-select-lg" v-model="newLocation"
                        @change="calendar.onLocationChanged(newLocation)">
                    <option :value="{ id: 0 }" disabled>Select Location...</option>
                    <option v-for="location in locationList" :key="location['city_id']" :value="location">
                        {{ location.name }}
                    </option>
                </select>
            </div>
        </div>
        <div class="d-flex flex-column flex-md-row flex-lg-row flex-xl-row" v-if="calendar.location.name">
            <CalMonth :calendar="calendar"/>
            <CalEventList v-if="calendar.selectedDate._dateText" :calendar="calendar"/>
        </div>
    </div>
</template>
<script>
import getCalendar from '../store';
import _ from 'lodash';
import cities from '../assets/js/cities';
import CalEventList from '../components/CalEventList.vue';
import CalMonth from '../components/CalMonth.vue';

export default {
    name: 'Calendar',
    components: {
        CalEventList,
        CalMonth
    },
    data() {
        return {
            newLocation: {id: 0},
            locationList: cities,
            calendar: getCalendar()
        }
    },
    created() {
        const vm = this,
            location = localStorage.location;

        if (location) {
            vm.newLocation = _.find(cities, city => city['city_id'] == location);
            vm.calendar.onLocationChanged(vm.newLocation);
        }
    }
}
</script>