<template>
    <div class="p-3">
        <div v-for="section in sections" :key="section.name">
            <div v-if="section.books && section.books.length > 0">
                <h2 class="pt-5 pb-2">{{ section.name }}</h2>
                <div class="d-flex flex-wrap">
                    <div v-for="book in section.books" :key="book.code" class="d-flex flex-column align-items-center mb-4">
                        <div class="card m-2 btn btn-outline-secondary p-0" @click="goToBook(book)">
                            <div class="card-img-top" :style="`background-image: url(${getImageUrl(book)})`"></div>
                            <div class="card-body d-flex align-items-center">
                                <h6 class="card-title flex-fill">{{ book.name }}</h6>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import gurus from '../assets/js/parampara';

export default {
    name: 'Books',
    data() {
        return {
            sections: [],
            selectedBook: false
        }
    },
    created() {
        var vm = this;
        vm.sections.push(gurus[1]);
        vm.sections.push(gurus[0]);
        vm.sections.push({
            name: 'Useful Books',
            books: [{
                name: 'Kirttan Guide - 8th Edition',
                code: 'KirttanGuide',
                isPdf: true
            }, {
                name: 'Daily Service Life Guide',
                code: 'ServiceLife',
                isPdf: true
            }]
        })
    },
    methods: {
        getImageUrl(book) {
            try {
                return require('@/assets/books/cover/' + book.code + '.jpg');
            } catch (e) {
                return '';
            }
        },
        goToBook(book) {
            const vm = this;

            if (!book.isPdf) {
                vm.$router.push(`books/${book.code}`);
                return;
            }

            vm.$router.push(`books/${book.code}?isPdf=true`);
        }
    }
}
</script>
<style lang="scss" scoped>
.card {
    max-width: 150px;
    min-width: 150px;
    height: 280px;
}

.card-img-top {
    min-height: 180px;
    max-height: 180px;
    width: 148px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position-x: center;
}

.btn-outline-secondary:hover {
    .themed-btn {
        color: #fff !important;
    }
}
</style>